@use '../../../helpers/inverters' as *;

.DropBox {
    background-color: rgba(black, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 31;

    .ImageUpdate {
        border: solid white 1px;
        width: 800px;
        height: 600px;
        background-color: white;
        border-radius: 20px;

        @include Breakpoints(small) {
            width: 80%;
            height: max-content;
            padding: 50px !important;
        }

        @include Breakpoints(mobile) {
            width: 90%;
            height: max-content;
            justify-content: center !important;
            align-content: center !important;
        }

        >svg {
            position: absolute;
            right: 7px;
            top: 9px;
            width: max-content;
            font-size: 27px;
            cursor: pointer;

            &:hover {
                color: $BaseColor;
            }
        }

        .ImageUpdate_Old,
        .ImageUpdate_New {
            height: 400px;

            @include Breakpoints(mobile) {
                height: 350px;
            }

            img {
                width: 200px;
                height: 200px;
                border-radius: 50%;
                object-fit: cover;
            }
        }

        input[type="submit"] {
            @include FormSubmit();
            @include linear(0.5s, linear, all);
            width: 60%;
            margin: auto;

            &:hover {
                transform: none;
                background-color: $HoverColor;
            }
        }
    }
}